@use './utilities/variables';
@use './utilities/mixins';

.error404 {
    #primary {
        padding: 0;
        @media (max-width: 991px) {
            padding: 0;
        }
    }
}
.error-page {
    height: 100vh;
    background: transparent url("../img/bg-404.webp") no-repeat 50% 0 / cover;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    &__subtitle {
        color: variables.$white;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 140%;
    }

    &__title {
        color: variables.$white;
        font-size: 180px;
        font-style: normal;
        font-weight: 700;
        line-height: 105%;
        margin: 0;
    }

    &__text {
        color: variables.$white;
        text-align: center;
        font-size: 20px;
        font-style: normal;
        font-weight: 500;
        line-height: 150%;
        max-width: 330px;
        margin: 0 auto 32px;
    }

    &__buttons {
        display: flex;
        justify-content: center;
    }

    &__btn {
        border-radius: 100px;
        background: variables.$yellow;
        max-width: 172px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        height: 44px;
        color: variables.$black;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 140%;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            border-radius: 100px;
            transition: all .3s ease-in-out;
            background: variables.$yellow-gradient;
            opacity: 0;
        }

        span {
            position: relative;
            z-index: 1;
        }

        @media all and (min-width: 1250px) {
            &:hover {
                background: variables.$ivory;
                color: variables.$black;
                background: variables.$yellow;

                &::after {
                    opacity: 1;
                }
            }
        }
    }
}
