@use '../utilities/variables';
@use '../utilities/mixins';

.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999;
  width: 100vw;
  height: 100vh;
  padding: 0 16px;

  &.open {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &.dev-popup {
    .modal__text {
      max-width: 356px;
    }
  }
  &__overlay {
    position: absolute;
    z-index: 1;
    background: rgba(0, 0, 0, .25);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  }

  &__content {
    max-width: 520px;
    width: 100%;
    position: relative;
    z-index: 2;
    background: #fff;
    border-radius: 20px;
    padding: 36px 32px;

    @media all and (max-width: 767px) {
      padding: 36px 16px;
    }
  }

  &__title {
    color: variables.$black;
    display: block;
    text-align: center;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin-bottom: 24px;
  }

  &__subtitle {
    color: variables.$black;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    margin: -17px 0 24px;
    display: block;
  }

  &__row {
    position: relative;
    margin-bottom: 14px;
  }

  &__label {
    position: absolute;
    left: 14px;
    top: 0;
    color: variables.$grey;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    pointer-events: none;
    transform: translateY(16px);
    transition: all .3s ease-in-out;

    &.active {
      font-size: 12px;
      transform: translateY(4px);
    }
  }

  &__show-pass {
    top: 17px;
    position: absolute;
    right: 14px;
    width: 17px;
    height: 16px;
    display: flex;
    align-items: center;
    justify-content: center;

    &.active {
      .eye-2 {
        display: block;
      }

      .eye-1 {
        display: none;
      }
    }
  }

  .error {
    color: #e90000;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    margin-top: 4px;
  }

  &__forgot-row {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 26px;
  }

  &__forgot-pass {
    color: variables.$grey;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    text-decoration: underline;

    @media (min-width: 1250px) {
      &:hover {
        color: variables.$grey;
        text-decoration: none;
      }
    }
  }

  &__btn {
    display: flex;
    height: 44px;
    padding: 0;
    justify-content: center;
    align-items: center;
    border: none;
    width: 100%;
    cursor: pointer;
    border-radius: 100px;
    background: #010101;
    color: #fff;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    transition: all .25s ease-in-out;

    @media (min-width: 1250px) {
      &:hover {
        color: variables.$yellow;
        background: #010101;
        text-decoration: none;
      }
    }
  }

  &__bottom {
    padding-top: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: variables.$grey;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
  }

  &__text {
    color: variables.$black;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    margin-bottom: 20px;
    max-width: 290px;
    margin: 0 auto 20px;
    text-align: center;
  }

  &__cont-btn {
    display: flex;
    height: 44px;
    padding: 0;
    justify-content: center;
    align-items: center;
    border: none;
    width: 100%;
    cursor: pointer;
    border-radius: 100px;
    background: #010101;
    color: #fff;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    transition: all .25s ease-in-out;

    @media (min-width: 1250px) {
      &:hover {
        color: variables.$yellow;
        background: #010101;
        text-decoration: none;
      }
    }
  }

  &__close {
    position: absolute;
    right: 20px;
    top: 20px;
    border: unset;
    outline: none;
    background: initial;
    cursor: pointer;

    &:hover {
      border: unset;
      outline: none;
      background: initial;
    }

    svg {
      display: block;
      fill: #010101;
    }

    @media (min-width: 1250px) {
      &:hover {
        svg {
          fill: #f00;
        }
      }
    }
  }

  &#cookies {
    align-items: flex-end;

    @media (min-width: 768px) {
      justify-content: flex-end;
    }

    .modal {
      &__content {
        padding: 34px 16px;
        bottom: 0;

        @media (max-width: 767px) {
          border-radius: 20px 20px 0 0;
        }

        @media (min-width: 768px) {
          padding: 24px;
          bottom: 20px;
          right: 45px;
          justify-content: flex-end;
        }

        @media (min-width: 1199px) {
          bottom: 40px;
          right: 65px;
        }
      }

      &__title {
        text-align: left;
        margin-top: 0;
        text-transform: unset;
        font-size: 16px;
        margin: 0 0 16px;
      }

      &__text {
        margin: 0 0 20px;
        padding: 0;
        width: 100%;
        max-width: 100%;
        text-align: justify;
        font-size: 14px;
      }

      &__close {
        top: 34px;
        right: 16px;

        @media (min-width: 768px) {
          top: 24px;
          right: 24px;
        }
      }

      &__btn {
        background: variables.$yellow;
        color: variables.$black;

        &:hover {
          background: variables.$yellow-gradient;
        }
      }
    }
  }
}

.age-holder {
  display: flex;
  background: #fff;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
  z-index: 1;
  max-width: 760px;
  width: 100%;
  align-items: center;

  @media (max-width: 767px) {
    flex-wrap: wrap;
    max-width: 340px;

    .modal__close svg {
      fill: #fff;
    }
  }

  &__image {
    max-width: 340px;
    flex-shrink: 0;
    width: 100%;
  }

  &__info {
    width: 100%;
    padding: 0 24px;

    @media (max-width: 767px) {
      padding: 20px 24px 24px;
    }
  }

  &__title {
    color: variables.$black;
    text-align: center;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin-bottom: 10px;
  }

  &__subtitle {
    color: variables.$black;
    text-align: center;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    margin-bottom: 10px;
  }

  &__text {
    color: variables.$black;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    max-width: 260px;
    margin: 0 auto 20px;
  }

  &__buttons {
    display: flex;
  }

  &__btn-no {
    margin-right: 6px;
    border-radius: 100px;
    background: #010101;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: #fff;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    max-width: 127px;
    flex-shrink: 0;
    width: 100%;
    border: none;
    transition: all .25s ease-in-out;

    @media all and (min-width: 1250px) {
      &:hover {
        color: variables.$yellow;
        background: #010101;
      }
    }
  }

  &__btn-yes {
    margin-right: 6px;
    border-radius: 100px;
    background: variables.$yellow;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: variables.$black;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    width: 100%;
    border: none;
    position: relative;
    transition: all .25s ease-in-out;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 100px;
      transition: all .3s ease-in-out;
      background: linear-gradient(275deg, #FFD233 0.95%, #FEEC49 47.84%, #FFD233 98.64%);
      opacity: 0;
    }

    span {
      position: relative;
      z-index: 1;
    }

    @media all and (min-width: 1250px) {
      &:hover {
        background: #f3f4f6;

        &::after {
          opacity: 1;
        }
      }
    }
  }
}

.sign-up-btn,
.sign-in-btn {
  margin-left: 5px;
  color: variables.$grey;
  text-decoration: underline;

  @media (min-width: 1250px) {
    &:hover {
      color: variables.$grey;
      text-decoration: none;
    }
  }
}

.eye-1 {
  display: block;
}

.eye-2 {
  display: none;
}
