@use "utilities/variables";
@use "utilities/mixins";
.scroll-help-ico {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 140%;
  border-radius: 12px;
  background: rgba(0, 0, 0, 0.60);
  backdrop-filter: blur(14px);
  padding: 8px 20px 8px 14px;
  align-items: center;
  display: flex;
  &.hide {
    display: none;
  }
  svg {
    margin-right: 8px;
  }
}
.bounce-animation {
  animation: hand 2s infinite;
}
@keyframes hand {
    0%,to {
        transform: translateY(0)
    }

    50% {
        transform: translateY(-16px)
    }
}
.episode-inner {
  padding: 80px 0 0;
  max-width: 1110px;
  width: 100%;
  margin: 0 auto;
  .breadcrumbs {
    margin: 0 0 90px;
    @media (max-width: 1250px) {
      margin: 0 0 40px;
    }
  }
  &__head {
    border-bottom: 1px solid #181818;
    background: #080809;
    padding: 14px 16px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    @media (max-width: 767px) {
      padding: 10px 16px;
    }
    &.hide {
      display: none;
    }
  }
  &__head-holder {
    max-width: 1110px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }
  &__head-box {
    display: flex;
    align-items: center;
  }
  &__back {
    width: 32px;
    height: 32px;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.10);
    backdrop-filter: blur(14px);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all .3s ease-in-out;
    svg {
      stroke: #fff;
      transition: all .3s ease-in-out;
    }
    @media (min-width: 1250px) {
      &:hover {
        background: variables.$yellow;
        svg {
          stroke: #000;
        }
      }
    }
  }
  &__title {
    margin-left: 24px;
    display: flex;
    align-items: center;
    @media (max-width: 767px) {
      margin-left: 12px;
      flex-wrap: wrap;
    }
  }
  &__name {
    color: #fff;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    margin: 0;
    @media (max-width: 767px) {
      font-size: 14px;
    }
  }
  &__descr {
    color: #9d9aa2;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    margin-left: 21px;
    position: relative;
    @media (max-width: 767px) {
      font-size: 12px;
      margin: 0;
      width: 100%;
    }
    &::after {
      content: '';
      position: absolute;
      left: -11px;
      top: 2px;
      width: 1px;
      height: 16px;
      background: #9d9aa2;
      @media (max-width: 767px) {
        top: 0;
        display: none;
      }
    }
  }
  &__list {
    max-width: 700px;
    margin: 0 auto;
    width: 100%;
  }
  &__bottom {
    max-width: 662px;
    padding: 0 16px 100px;
    margin: 60px auto 0;
    .comic-list__item {
      padding: 16px 20px 16px 14px;
      border-bottom: 1px solid #feec49;
      @media (max-width: 767px) {
        padding: 8px 10px 8px 8px;
      }
    }
    @media (max-width: 767px) {
      margin: 46px auto 0;
    }
  }
  &__bottom-title {
    color: variables.$black;
    font-size: 28px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin-bottom: 14px;
    @media (max-width: 767px) {
      font-size: 20px;
      margin-bottom: 10px;
    }
  }
  &__controls {
    position: fixed;
    background: #0d0c0c;
    backdrop-filter: blur(27px);
    bottom: 20px;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 14px 28px;
    max-width: 400px;
    margin: 0 auto;
    border-radius: 100px;
    &.hide {
      display: none;
    }
  }
  &__prev {
    display: flex;
    align-items: center;
    color: #fff;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    transition: all .3s ease-in-out;
    svg {
      margin-right: 10px;
      stroke: #fff;
      transition: all .3s ease-in-out;
    }
    &.disabled {
      color: #9d9aa2;
      pointer-events: none;
      svg {
        stroke: #9d9aa2;
      }
    }
    @media (min-width: 1250px) {
      &:hover {
        svg {
          stroke: #feec49;
        }
      }
    }
  }
  &__next {
    display: flex;
    align-items: center;
    color: #fff;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    transition: all .3s ease-in-out;
    svg {
      margin-left: 10px;
      stroke: #fff;
      transition: all .3s ease-in-out;
    }
    &.disabled {
      color: #9d9aa2;
      pointer-events: none;
      svg {
        stroke: #9d9aa2;
      }
    }
    @media (min-width: 1250px) {
      &:hover {
        svg {
          stroke: #feec49;
        }
      }
    }
  }
  &__all {
    display: flex;
    align-items: center;
    color: #fff;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    transition: all .3s ease-in-out;
    svg {
      margin-right: 4px;
      fill: #fff;
      transition: all .3s ease-in-out;
    }
    @media (min-width: 1250px) {
      &:hover {
        svg {
          fill: #feec49;
        }
      }
    }
  }
}
.comic-list {
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 12px;
    background: #fff;
    box-shadow: 0px 1px 30px 0px rgba(61, 61, 61, 0.08);
    padding: 8px 10px 8px 8px;
    text-decoration: none;
    border-bottom: 1px solid transparent;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    @media (min-width: 1250px) {
      &:hover {
        border-color: variables.$yellow;
        .comic-list__img img {
          transform: scale(1.06);
        }
      }
    }
  }
  &__content {
    display: flex;
    align-items: center;
  }
  &__img {
    border-radius: 10px;
    overflow: hidden;
    img {
      transition: all .3s ease-in-out;
    }
  }
  &__content-left {
    margin-left: 10px;
  }
  &__title-desc {
    color: variables.$black;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    margin-bottom: 2px;
  }
  &__content-next {
    border-radius: 100px;
    background: variables.$yellow;
    display: flex;
    align-items: center;
    color: variables.$black;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    padding: 0 20px;
    height: 44px;
    max-width: 230px;
    width: 100%;
    position: relative;
    @media (max-width: 767px) {
      max-width: none;
      width: auto;
      min-width: 80px;
      padding: 0 16px;
      svg {
        margin-left: 5px;
      }
    }
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 100px;
      transition: all .3s ease-in-out;
      background: linear-gradient(275deg, #FFD233 0.95%, #FEEC49 47.84%, #FFD233 98.64%);
      opacity: 0;
    }
    span {
      position: relative;
      z-index: 1;
      display: flex;
      justify-content: space-between;
      width: 100%;
      align-items: center;
      @media (max-width: 767px) {
        font-size: 0;
        &::before {
          content: attr(data-mobile);
          font-size: 14px;
        }
      }
    }
    @media (min-width: 1250px) {
      &:hover {
        &::after {
          opacity: 1;
        }
      }
    }
  }
}
.to-top {
  position: fixed;
  bottom: 100px;
  right: 50px;
  width: 52px;
  height: 52px;
  z-index: 10;
  border-radius: 14px;
  border: 2px solid #f3f4f6;
  background: #fff;
  display: none;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  color: variables.$grey;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  text-transform: uppercase;
  transition: all .3s ease-in-out;
  cursor: pointer;
  @media (max-width: 1250px) {
    bottom: 60px;
    right: 15px;
  }
  &.show {
    display: flex;
  }
  @media (min-width: 1250px) {
    &:hover {
      border-color: variables.$yellow;
    }
  }
  &__center {
    text-align: center;
  }
  svg {
    margin-bottom: 3px;
  }
  span {
    display: block;
  }
}
