@use '../utilities/variables';
@use '../utilities/mixins';
form {
    input {
      display: flex;
      height: 50px;
      padding: 16px 14px 4px;
      border-radius: 10px;
      background: #f3f4f6;
      width: 100%;
      border: 1px solid #f3f4f6;
      outline: none;
      color: variables.$black;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
      &.error-input {
        border-color: #e90000;
      }
    }
    .inputs-wrapper {
        margin-bottom: 16px;
    }
}
