@use '../utilities/variables';
@use '../utilities/mixins';
.contacs-page main {
  @media (max-width: 767px) {
    padding-top: 82px;
  }
}
.page-title h1 {
  margin: 0;
}
.page-title {
  margin-bottom: 40px;
  @media (max-width: 767px) {
    margin-bottom: 24px;
  }
}
.contacts-info {
  .container {
    display: flex;
    @media (max-width: 767px) {
      flex-wrap: wrap;
    }
  }
  &__item {
    & + .contacts-info__item {
      margin-top: 30px;
    }
    a {
      @media (min-width: 1250px) {
        &:hover {
          color: #febc49;
        }
      }
      @media (max-width: 767px) {
        &:active {
          color: #febc49;
        }
      }
    }
  }
  &__title {
    color: variables.$black;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin-bottom: 12px;
    @media (max-width: 767px) {
      font-size: 16px;
    }
  }
  &__email {
    display: flex;
    align-items: center;
    color: variables.$black;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    text-decoration: underline;
    @media (max-width: 767px) {
      font-size: 14px;
    }
    svg {
      margin-right: 6px;
    }
  }
}
.social-list {
  margin-left: 104px;
  @media (max-width: 767px) {
    margin: 28px 0 0;
  }
  &__title {
    color: variables.$black;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin-bottom: 10px;
    @media (max-width: 767px) {
      font-size: 16px;
    }
  }
}
