@use './utilities/variables';

.maintenance {
    padding: 0;
    margin: 0;
    color: variables.$white;
    font-family: "Lufga", sans-serif;
    .container {
        margin: 0 auto;
        width: 100%;
        max-width: 1140px;
    }
    &-desktop {
        background: url("../img/maintenance-desktop.webp") no-repeat center top / cover;
        h1 {
            font-size: 52px;
        }
    }
    &-mobile {
        background: url("../img/maintenance-mobile.webp") no-repeat center top / cover;
        & > header,  & > main {
            text-align: center;
        }
        h1 {
            font-size: 42px;
        }
    }
    & > header {
        padding: 37px 0;
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
    }
    & > main {
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;

        a {
            color: variables.$white;
            padding-left: 20px;
            position: relative;

            &:before {
                content: '';
                display: block;
                width: 15px;
                height: 13px;
                position: absolute;
                left: 0;
                top: calc(50% - 6.5px);
                background: transparent url("data:image/svg+xml;base64,") no-repeat center center / contain;
            }
        }
    }
}
