@use '../utilities/variables';
@use '../utilities/mixins';
.faq-item {
  border-radius: 10px;
  background: #fff;
  box-shadow: 0px 1px 30px 0px rgba(61, 61, 61, 0.08);
  border-bottom: 1px solid #fff;
  max-width: 922px;
  & + .faq-item {
    margin-top: 10px;
  }
}
.faq-item.active {
  padding-bottom: 14px;
  border-color: variables.$yellow;
}
.faq-question {
  padding: 14px 36px 14px 16px;
  color: variables.$black;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  cursor: pointer;
  position: relative;
  margin: 0;
  transition: all 0.3s ease-in-out;
  @media (min-width: 1250px) {
    &:hover {
      font-weight: 500;
    }
  }
}
.faq-question::after {
  border-bottom: 1px solid #010101;
  border-right: 1px solid #010101;
  content: '';
  display: block;
  width: 5.5px;
  height: 5.5px;
  right: 16px;
  margin-top: -4px;
  pointer-events: none;
  position: absolute;
  top: 50%;
  -webkit-transform-origin: 66% 66%;
  transform-origin: 66% 66%;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  transition: all 0.3s ease-in-out;
}
.faq-item.active .faq-question::after {
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
}
.faq-answer {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.4s ease;
  padding: 0 16px;
  font-size: 14px;
  line-height: 150%;
  position: absolute;
  font-family: 'Manrope';
  font-weight: 400;
  left: -9999px;
}
.faq-item.active .faq-answer {
  max-height: 1000px;
  left: 0;
  position: relative;
  margin-top: -8px;
}
.faq-item.active .faq-question {
  font-weight: 500;
}
