@use 'utilities/variables';
@use 'utilities/mixins';
@use 'parts/slider';
@use 'parts/genres-inner';

.home-logo {
  margin: 0;
  font-size: 10px;
  span {
    position: absolute;
    left: -9999px;
    display: block;
  }
}
.logo {
  margin-top: -4px;
}
.home {
  .comics-grid,
  .comics {
    &-slide {
      width: 100%;
    }
    &-grid {
      margin-bottom: 0;
      width: 100%;
      @media (max-width: 1250px) {
        margin-right: -16px;
        margin-left: -16px;
        padding-left: 16px;
        width: auto;
      }
    }
  }
  main {
    padding-top: 66px;
  }
    .section {
        margin-bottom: 54px;
        @media (max-width: 767px) {
          margin-bottom: 36px;
          &.hot {
            margin-bottom: 78px;
          }
        }
        &.section-slider {
          margin-bottom: 4px;
          overflow: hidden;
          @media (max-width: 767px) {
            margin-bottom: 0;
            padding-bottom: 40px;
            padding-left: 16px;
          }
        }
        h2 {
            &.section-title {
                line-height: 140%;
                display: flex;
                align-items: center;
                float: left;
                margin-bottom: 16px;
                @media (max-width: 767px) {
                  margin-bottom: 8px;
                }
                svg {
                  margin-right: 10px;
                  @media (max-width: 767px) {
                    width: 22px;
                    margin-right: 4px;
                  }
                }
                sub {
                  font-size: 16px;
                  font-weight: 400;
                  line-height: 140%;
                  margin-left: 6px;
                  @media (max-width: 767px) {
                    font-size: 14px;
                    bottom: -1px;
                  }
                }
            }
        }
        .see-all {
          line-height: 30px;
          float: right;
        }
        &#discount-40 {
            h2 {
                text-transform: uppercase;
            }
            .button {
                width: 100%;
                max-width: 216px;
                align-self: center;
                justify-content: center;
                padding: 10px 16px;
            }
        }
        &#exclusive-titles {
            h5 {
                margin: 0;
            }
        }
    }
}
