@use 'utilities/variables';
@use 'utilities/mixins';
@use 'parts/genres-inner';
.single-comics {
  main {
    padding-top: 82px;
    @media (max-width: 767px) {
      padding-top: 66px;
    }
  }
}
.label {
    align-items: center;
    display: flex;
    gap: 4px;
    &__item {
        border-radius: 8px 0;
        background: #ffc68c;
        padding: 2px 4px;
        text-align: center;
        color: variables.$white;
        &--public {
            align-items: center;
            color: variables.$black;
            color: #fff;
            display: flex;
            justify-content: center;

            svg {
                margin-right: 4px;
            }
        }
    }
}

.comic {
    &-general {
        &-inner {
            position: relative;

            @media (min-width: 992px) {
                align-items: center;
                display: flex;
                gap: 30px
            }

            @media (min-width: 1200px) {
                gap: 40px
            }
        }

        &-img {
            margin: 0 -16px 10px;
            position: relative;

            @media (min-width: 992px) {
                margin: 0;
                order: 1;
                width: 50%;
            }

            @media (min-width: 1200px) {
                flex: 1 0 640px;
            }

            img {
                height: auto;
                width: 100%;

                @media (min-width: 992px) {
                    border: 2px solid variables.$black;
                    border-radius: 24px;
                    filter: drop-shadow(4px 4px 0 variables.$black);
                }
            }
        }

        &__labels {
            align-items: center;
            flex-wrap: wrap;
            .label__item {
                font-size: 12px;
                font-style: normal;
                font-weight: 400;
                line-height: 16px;

                &--uncensored {
                    font-weight: 700;
                }
            }
        }

        &__desc {
            font-size: 14px;
            margin-bottom: 0;

            @media (min-width: 992px) {
                max-height: 146px;
                overflow-x: hidden;
                overflow-y: scroll;
                -ms-overflow-style: none;
                margin-right: 6px;
                padding-right: 6px;
                scrollbar-width: none
            }

            &::-webkit-scrollbar {
                border-radius: 999px;
                width: 4px;

                &-track {
                    box-shadow: inset 0 0 2px transparent
                }

                &-thumb {
                    background-clip: padding-box;
                    background-color: variables.$black;
                    border-radius: 10px
                }
            }
        }

        .tags-wrapper {
            margin-bottom: 8px;

            .comic-tag {
                color: variables.$devil-black;
                display: inline-block;
                font-size: 14px;
                font-style: normal;
                font-weight: 500;
                line-height: 16px;
                margin-bottom: 8px;
                margin-right: 8px;
                text-decoration-line: underline;

                &.hidden {
                    display: none
                }
            }

            .show-more {
                background: transparent;
                border: none;
                color: variables.$black;
                cursor: pointer;
                font-size: 14px;
                font-style: normal;
                font-weight: 500;
                line-height: 16px;
                text-decoration-line: underline;

                &:before {
                    color: variables.$devil-black;
                    content: " . . . ";
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 700;
                    line-height: 16px;
                    margin-right: 8px;
                    text-decoration-line: none
                }
            }

            &.show-all {
                .show-more {
                    &:before {
                        content: none
                    }
                }
            }
        }

        &__story-art {
            font-size: 14px;
            line-height: 16px;
            color: variables.$devil-black;

            span {
                color: variables.$black;
                font-weight: 700;
            }

            a {
                color: variables.$devil-black;
                font-weight: 500;
                text-decoration: underline;
                position: relative;
                margin-right: 8px;

                &:after {
                    content: "/";
                    position: absolute;
                    right: -9px;
                }

                &:last-of-type:after {
                    content: none;
                }
            }
        }
    }

    &__btn {

        &-favorite,
        &-rate {
            background: transparent;
            border: 0;
            font-size: 14px;
            outline: none;
            padding: 0;

            svg {
                height: 20px;
                width: auto;

                @media (min-width: 768px) {
                    height: 24px;
                }
            }

            .on {
                display: none;
            }

            >span {
                display: flex;
                gap: 8px;
                align-items: center;
                color: variables.$black;
            }

            &.in-favorite,
            &.rated {
                .off {
                    display: none;
                }

                .on {
                    display: inline-flex;
                }
            }
        }

        &_uncensored {
            img {
                margin-right: 9px;
            }
        }

        @media (min-width: 768px) {
            padding: 14px 24px;
        }

        &s {
            display: flex;
            flex-direction: column;
            gap: 8px;
            align-items: flex-start;
            justify-content: flex-start;

            @media (max-width: 992px) {
                .btn {
                    width: 100%;
                }
            }
        }
    }
}

.noten {
    .comic-general {
        &__story-art {
            font-size: 12px;

            @media (min-width: 768px) {
                font-size: 14px;
            }
        }
    }
}
.section:not(.no-padding).comic-general {
  @media (max-width: 767px) {
    padding: 0;
  }
}
.comic-general {
  @media (max-width: 767px) {
    padding: 0;
  }
  &__top {
    margin: 0 0 24px;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 1023px) {
      align-items: flex-start;
    }
    @media (max-width: 767px) {
      flex-wrap: wrap;
    }
  }
  &__image {
    position: relative;
    border-radius: 16px;
    overflow: hidden;
    max-width: 680px;
    width: 100%;
    @media (min-width: 768px) {
      order: 1;
    }
    @media (max-width: 767px) {
      border-radius: 0;
      margin: 0 -16px 16px;
      width: auto;
      max-width: none;
    }
  }
  &__info {
    max-width: 398px;
    width: 100%;
    @media (min-width: 768px) {
      margin-right: 15px;
    }
    .comic-general__labels {
      justify-content: space-between;
      align-items: center;
    }
    .views {
      border-radius: 100px;
      background: #f3f4f6;
      backdrop-filter: blur(14px);
      padding: 0 6px;
      height: 24px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: variables.$black;
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 150%;
      span {
        display: flex;
        align-items: center;
        justify-content: center;
      }
      svg {
        margin-right: 2px;
      }
    }
  }
  &__info-bottom {
    margin-bottom: 20px;
    @media (max-width: 767px) {
      margin-bottom: 6px;
    }
  }
  &__title {
    color: variables.$black;
    font-size: 44px;
    font-style: normal;
    font-weight: 700;
    line-height: 105%;
    margin: 0 0 22px;
    @media (max-width: 767px) {
      font-size: 20px;
      font-weight: 500;
      margin: 0 0 6px;
    }
  }
  &__add-info {
    display: flex;
    align-items: center;
    margin: 0;
  }
  &__rating-stars {
    color: variables.$black;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    border-radius: 100px;
    background: #f3f4f6;
    backdrop-filter: blur(14px);
    padding: 0 6px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 4px;
    svg {
      margin-right: 2px;
    }
  }
  &__rating-stars-rating {
    span {
      color: variables.$grey;
      font-size: 10px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
    }
  }
  &__old {
    border-radius: 100px;
    background: linear-gradient(81deg, #F44043 3.28%, #C70028 94.73%);
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 34px;
    color: #fff;
    text-align: center;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    margin-left: 4px;
    padding-left: 1px;
  }
  &__info-items {
    display: flex;
    .label__item {
      line-height: 20px;
      & + .label__item {
        margin-left: 4px;
      }
    }
  }
}
.container.comic-list {
  margin-bottom: 90px;
  @media (min-width: 1024px) {
    display: flex;
    justify-content: space-between;
    overflow: visible;
    align-items: flex-start;
  }
  @media (max-width: 767px) {
    margin-bottom: 36px;
  }
}
.comic-list-inner {
  max-width: 680px;
  width: 100%;
  padding-top: 6px;
  @media (max-width: 1023px) {
    max-width: 100%;
  }
}
.comic-list-sidebar {
  padding: 0;
  width: 100%;
  @media (min-width: 1024px) {
    position: sticky;
    top: 80px;
    max-width: 398px;
    margin-left: 15px;
    .comic-archive-item {
      display: flex;
      align-items: center;
      & + .comic-archive-item {
        margin-top: 16px;
      }
    }
    .comic-content {
      margin-left: 12px;
    }
  }
  @media (max-width: 1023px) {
    display: none;
  }
  &__title {
    color: variables.$black;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin: 0 0 14px;
  }
  .comic-archive-item > a {
    max-width: 110px;
    flex-shrink: 0;
  }
  div.comics .comic-title,
  article.comics .comic-title {
    font-weight: 400;
    margin-bottom: 12px;
    height: auto;
    font-size: 16px;
    @media (max-width: 767px) {
      font-size: 12px;
      margin-bottom: 5px;
    }
  }
  div.comics .comic-descr,
  article.comics .comic-descr {
    overflow: hidden;
    color: variables.$black;
    display: -webkit-box;
   -webkit-line-clamp: 2;
   -webkit-box-orient: vertical;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    font-family: 'Manrope';
    @media (max-width: 767px) {
      font-size: 10px;
    }
  }
}
.description-wrapper {
  margin-bottom: 10px;
  overflow: hidden;
  color: variables.$black;
  font-family: 'Manrope';
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  p {
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}
.comics-list-head__quantity {
  color: variables.$black;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  margin: 0;
}
.comic-list-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  &__sort-by {
    margin: 0;
    display: flex;
    align-items: center;
    color: variables.$black;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    cursor: pointer;
    &.active {
      svg {
        transform: rotate(180deg);
      }
    }
    svg {
      margin-right: 6px;
      flex-shrink: 0;
    }
  }
}
.comic-list {
  &__btn-box {
    text-align: right;
    @media (max-width: 767px) {
      text-align: center;
    }
  }
  &__resume {
    padding: 7px 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 100px;
    min-height: 32px;
    border-radius: 100px;
    background: variables.$yellow;
    color: #4c4c4c;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    margin-top: 6px;
    position: relative;
    svg {
      margin-left: 4px;
    }
    @media (max-width: 767px) {
      font-size: 0;
      width: 32px;
      height: 32px;
      min-width: 32px;
      svg {
        margin: 0;
      }
    }
    span {
      position: relative;
      z-index: 1;
      display: flex;
      align-items: center;
    }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 100px;
      background: linear-gradient(275deg, #FFD233 0.95%, #FEEC49 47.84%, #FFD233 98.64%);
      opacity: 0;
      transition: all .3s ease-in-out;
    }
  }
  &__completed {
    border-radius: 100px;
    background: #f3f4f6;
    padding: 9px;
    color: #4c4c4c;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 6px;
    min-height: 32px;
    svg {
      margin-left: 4px;
    }
    @media (max-width: 767px) {
      font-size: 0;
      width: 32px;
      height: 32px;
      min-width: 32px;
      svg {
        margin: 0;
      }
    }
  }
  &__more {
    display: flex;
    justify-content: center;
    margin-top: 14px;
  }
  &__more-btn {
    cursor: pointer;
    color: variables.$grey;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    display: flex;
    cursor: pointer;
    align-items: center;
    background: none;
    padding: 0;
    border: none;
    min-height: 0;
    @media (min-width: 1250px) {
      &:hover {
        color: #febc49;
        background: none;
        svg {
          stroke: #febc49;
        }
      }
    }
    svg {
      margin-left: 5px;
      stroke: #4c4c4c;
    }
  }
  &__item {
    display: flex;
    justify-content:space-between;
    align-items: center;
    border-radius: 12px;
    background: #fff;
    box-shadow: 0px 1px 30px 0px rgba(61, 61, 61, 0.08);
    padding: 8px 10px 8px 8px;
    text-decoration: none;
    border-bottom: 1px solid transparent;
    transition: all .3s ease-in-out;
    &.comic-list__item--completed {
      background: #f9f9f9;
      box-shadow: none;
    }
    @media (min-width: 1250px) {
      &:hover {
        border-bottom-color: variables.$yellow;
        .comic-list__img img {
          transform: scale(1.06);
        }
        .comic-list__resume::before {
          opacity: 1;
        }
      }
    }
    & + .comic-list__item {
      margin-top: 6px;
    }
    &--free {
      .comic-list__btn {
        color: #9d9aa2;
      }
    }
    &--read {
      .comic-list__btn {
        color: #31d676;
      }
    }
  }
  &__content {
    display: flex;
    align-items: center;
  }
  &__img {
    border-radius: 10px;
    overflow: hidden;
    img {
      transition: all .3s ease-in-out;
    }
  }
  &__content-left {
    margin-left: 10px;
  }
  &__title-desc {
    color: variables.$black;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    margin-bottom: 2px;
  }
  &__published {
    color: #9d9aa2;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
  }
  &__btn {
    color: #9d9aa2;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
  }
}
.genres-cus-title {
  color: variables.$black;
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  margin-bottom: 12px;
  @media (max-width: 767px) {
    font-size: 20px;
  }
}
.comics-add-box {
  @media (max-width: 1023px) {
    display: flex;
    justify-content: space-between;
    .comic-archive-item {
      max-width: 32%;
    }
  }
  div.comics .comic-thumbnail .views,
  article.comics .comic-thumbnail .views {
    line-height: 140%;
    font-weight: 400;
    padding: 0 0 4px 4px;
  }
  div.comics .comic-thumbnail .views > span,
  article.comics .comic-thumbnail .views > span {
    padding: 1px 4px;
    font-size: 10px;
    height: 16px;
    line-height: 16px;
  }
  div.comics .comic-thumbnail .episodes,
  article.comics .comic-thumbnail .episodes {
    font-size: 10px;
    font-weight: 500;
    border-radius: 10px 0px;
    background: rgba(1, 1, 1, 0.50);
    backdrop-filter: blur(14px);
    height: 18px;
  }
  div.comics .comic-thumbnail .label.adult,
  article.comics .comic-thumbnail .label.adult {
    font-size: 10px;
    padding: 3px 4.5px;
  }
}
.tag-list {
  margin: -8px 0 0 -8px;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
  color: variables.$grey;
  text-overflow: ellipsis;
  font-family: 'Manrope';
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  li {
    margin: 8px 0 0 8px;
  }
  a {
    color: variables.$grey;
    text-decoration: none;
    @media (min-width: 1250px) {
      &:hover {
        color: #101010;
      }
    }
  }
}
.also-like {
  margin-bottom: 80px;
  margin-top: 90px;
  @media (max-width: 767px) {
    margin-top: 66px;
    margin-bottom: 66px;
  }
}
.start-read {
  display: flex;
  align-items: center;
  border-radius: 100px;
  background: variables.$yellow;
  color: variables.$black;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 140%;
  padding: 0 12px 0 20px;
  height: 44px;
  max-width: 300px;
  position: relative;
  margin-top: 20px;
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 100px;
    transition: all .3s ease-in-out;
    background: linear-gradient(275deg, #FFD233 0.95%, #FEEC49 47.84%, #FFD233 98.64%);
    opacity: 0;
  }
  > span {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 1;
    width: 100%;
    svg {
      display: block;
      margin-left: 12px;
    }
  }
  @media all and (min-width: 1250px) {
      &:hover {
        background: variables.$yellow;
        color: variables.$black;
        &::after {
          opacity: 1;
        }
      }
  }
}
