@use '../utilities/variables';
@use '../utilities/mixins';
body {
    font-family: "Lufga", sans-serif;
    background: variables.$white;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 500;
    margin-bottom: 16px;
    line-height: 1.25;

    strong {
        font-weight: 800;
    }
}

h1 {
    font-size: 40px;

    &.page-title {
        font-weight: 900;
        text-transform: uppercase;
    }
}

h2 {
    font-size: 32px;
    @media (max-width: 767px) {
      font-size:24px;
    }
}

h3 {
    font-size: 28px;
    @media (max-width: 767px) {
      font-size: 20px;
    }
}

h4 {
    font-size: 24px;
    @media (max-width: 767px) {
      font-size: 16px;
    }
}

h5 {
    font-size: 20px;
    @media (max-width: 767px) {
      font-size: 12px;
    }
}

h6 {
    font-size: 18px;
    @media (max-width: 767px) {
      font-size: 10px;
    }
}

ol,
ul {
    padding-left: 20px;
}

a {
    text-decoration: unset;
    cursor: pointer;
    @media (min-width: 1250px) {
      &:hover {
          color: variables.$yellow;
      }
    }
}

button,
.button {
    border-radius: 8px;
    background: #f3f4f6;
    padding: 0 18px;
    min-height: 36px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: variables.$black;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    @media (min-width: 1250px) {
      &:hover {
          color: variables.$black;
          background: variables.$yellow;
      }
    }
}

img {
    max-width: 100%;
}
main {
  padding-top: 110px;
  padding-bottom: 60px;
  @media (max-width: 767px) {
    padding-top: 71px;
    position: relative;
    width: 100%;
    overflow: hidden;
  }
}
.container {
    margin: auto;
    max-width: 1142px;
    width: 100%;
    padding: 0 16px;
}

#primary {
    min-height: 70vh;
}
#page-title h1 {
  margin: 0;
  @media (max-width: 767px) {
      font-size: 24px;
  }
}
.section {
    .container {
        position: relative;
        padding: 0;
        max-width: 1110px;
    }

    &.dark {
        color: variables.$white;
    }

    &-head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 0 20px;
        @media (max-width: 767px) {
            margin: 0 0 8px;
        }
        h2 {
            margin: 0;
        }
    }

    &-title {
        font-size: 20px;

        @media (min-width: 768px) {
            font-size: 28px;
        }
    }

    &.text {
        &.disclaimer {
            margin: 20px auto 60px;
            color: variables.$black;
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            line-height: 150%;
            font-family: variables.$secondary-font;
            &:last-child {
              margin: 0 auto;
              padding-bottom: 20px;
            }
            @media (max-width: 767px) {
                font-size: 14px;
            }
        }
        h2,
        h3,
        h4,
        h5,
        h6 {
            margin: 24px 0 8px;
            font-family: 'Lufga';
            &:first-child {
              margin-top: 0;
            }
        }
        h2,
        h3 {
          font-size: 28px;
          @media (max-width: 767px) {
              font-size: 20px;
          }
        }
        h4,
        h5,
        h6 {
          font-size: 20px;
          @media (max-width: 767px) {
              font-size: 16px;
          }
        }
        ol {
          li {
            & + li {
              margin-top: 7px;
            }
          }
        }
        ul {
          margin: 8px 0 24px;
          padding: 0;
          list-style: none;
          li {
            position: relative;
            padding-left: 12px;
            & + li {
              margin-top: 7px;
            }
            &::after {
              content: '';
              position: absolute;
              left: 0;
              top: 9px;
              width: 4px;
              height: 4px;
              background: #010101;
              border-radius: 50%;
            }
          }
        }
    }
    &.comics {
        .slick {
            &-slide {
                margin: 0 8px;
                &r {
                    margin: 0 -8px;
                    clear: both;
                    min-width: 100%;
                    @media (min-widtH: 1199px) {
                        width: calc(100% + 16px);
                    }
                }
                & > div {
                    &:not(:last-of-type) {
                        margin-bottom: 18px;
                    }
                }
            }
            &-arrow {
                width: 42px;
                height: 42px;
                padding: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 100%;
                border: unset;
                background: variables.$button-white;
                position: absolute;
                top: calc(50% - 21px);
                cursor: pointer;
                &:hover,
                &:focus {
                    background: variables.$yellow;
                    outline: unset;
                }
                &.slick {
                    &-prev {
                        left: -75px;
                    }
                    &-next {
                        right: -75px;
                    }
                }
            }
        }
    }
}

.section:not(.no-padding),
aside {
    padding: 0 16px;
}

.comics {
    &-grid {
        display: grid;
        gap: 6px;
        grid-template-columns: repeat(6, 1fr);
        min-width: 100%;
        &.double-line {
            grid-template-columns: repeat(6, 2fr);
            @media (max-width: 1250px) {
              display: grid;
              grid-template-rows: repeat(2, 267px);
              grid-auto-flow: column;
              overflow-x: auto;
              width: auto;
              &::-webkit-scrollbar {
                height: 4px;
                background-color: #e0e0e0;
                border-radius: 4px;
              }

              &::-webkit-scrollbar-thumb {
                border-radius: 10px;
                background: #9d9aa2;
              }

              &::-webkit-scrollbar-track {
                background-color: #f3f4f6;
                border-radius: 4px;
              }
            }
            .comics {
              min-width: 128px;
            }
        }

        @media (max-width: 1023px) {
            grid-template-columns: repeat(3, 1fr);
            padding-bottom: 15px;
        }

        @media (min-width: 1024px) {
            gap: 16px 12px;
        }

        .comics {
            @media all and (min-width: 768px) {
                min-width: initial;
            }
        }
    }
}

.modal {
    display: none;
    position: fixed;
    top: 0;
    height: 0;
    z-index: 999;
    width: 100vw;
    height: 100vh;
    padding: 0 16px;
    &.open {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &.dev-popup {
      .modal__text {
        max-width: 356px;
      }
    }
    &__overlay {
        position: absolute;
        z-index: 1;
        background: rgba(0, 0, 0, .25);
        width: 100%;
        height: 100%;
    }
    &__content {
        max-width: 520px;
        width: 100%;
        position: relative;
        z-index: 2;
        background: #fff;
        border-radius: 20px;
        padding: 36px 32px;
        @media all and (max-width: 767px) {
            padding: 36px 16px;
        }
    }
    &__title {
      color: variables.$black;
      display: block;
      text-align: center;
      font-size: 20px;
      font-style: normal;
      font-weight: 500;
      line-height: 150%;
      margin-bottom: 24px;
    }
    &__subtitle {
      color: variables.$black;
      text-align: center;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
      margin: -17px 0 24px;
      display: block;
    }
    &__row {
      position: relative;
      margin-bottom: 14px;
    }
    &__label {
      position: absolute;
      left: 14px;
      top: 0;
      color: variables.$grey;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
      pointer-events: none;
      transform: translateY(16px);
      transition: all .3s ease-in-out;
      &.active {
        font-size: 12px;
        transform: translateY(4px);
      }
    }
    &__show-pass {
      top: 17px;
      position: absolute;
      right: 14px;
      width: 17px;
      height: 16px;
      display: flex;
      align-items: center;
      justify-content: center;
      &.active {
        .eye-2 {
          display: block;
        }
        .eye-1 {
          display: none;
        }
      }
    }
    .error {
      color: #e90000;
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 150%;
      margin-top: 4px;
    }
    &__forgot-row {
      display: flex;
      justify-content: flex-end;
      margin-bottom: 26px;
    }
    &__forgot-pass {
      color: variables.$grey;
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 150%;
      text-decoration: underline;
      @media (min-width: 1250px) {
        &:hover {
          color: variables.$grey;
          text-decoration: none;
        }
      }
    }
    &__btn {
      display: flex;
      height: 44px;
      padding: 0;
      justify-content: center;
      align-items: center;
      border: none;
      width: 100%;
      cursor: pointer;
      border-radius: 100px;
      background: #010101;
      color: #fff;
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: 140%;
      transition: all .25s ease-in-out;
      @media (min-width: 1250px) {
        &:hover {
          color: variables.$yellow;
          background: #010101;
          text-decoration: none;
        }
      }
    }
    &__bottom {
      padding-top: 24px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: variables.$grey;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
    }
    &__text {
      color: variables.$black;
      text-align: center;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
      margin-bottom: 20px;
      max-width: 290px;
      margin: 0 auto 20px;
      text-align: center;
    }
    &__cont-btn {
      display: flex;
      height: 44px;
      padding: 0;
      justify-content: center;
      align-items: center;
      border: none;
      width: 100%;
      cursor: pointer;
      border-radius: 100px;
      background: #010101;
      color: #fff;
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: 140%;
      transition: all .25s ease-in-out;
      @media (min-width: 1250px) {
        &:hover {
          color: variables.$yellow;
          background: #010101;
          text-decoration: none;
        }
      }
    }
    &__close {
      position: absolute;
      right: 20px;
      top: 20px;
      cursor: pointer;
      svg {
        display: block;
        fill: #010101;
      }
      @media (min-width: 1250px) {
        &:hover {
          svg {
            fill: #f00;
          }
        }
      }
    }
}
.age-holder {
  display: flex;
  background: #fff;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
  z-index: 1;
  max-width: 760px;
  width: 100%;
  align-items: center;
  @media (max-width: 767px) {
    flex-wrap: wrap;
    max-width: 340px;
    .modal__close svg {
      fill: #fff;
    }
  }
  &__image {
    max-width: 340px;
    flex-shrink: 0;
    width: 100%;
  }
  &__info {
    width: 100%;
    padding: 0 24px;
    @media (max-width: 767px) {
      padding: 20px 24px 24px;
    }
  }
  &__title {
    color: variables.$black;
    text-align: center;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin-bottom: 10px;
  }
  &__subtitle {
    color: variables.$black;
    text-align: center;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    margin-bottom: 10px;
  }
  &__text {
    color: variables.$black;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    max-width: 260px;
    margin: 0 auto 20px;
  }
  &__buttons {
    display: flex;
  }
  &__btn-1 {
    margin-right: 6px;
    border-radius: 100px;
    background: #010101;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: #fff;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    max-width: 127px;
    flex-shrink: 0;
    width: 100%;
    border: none;
    transition: all .25s ease-in-out;
    @media all and (min-width: 1250px) {
        &:hover {
          color: variables.$yellow;
          background: #010101;
        }
    }
  }
  &__btn-2 {
    margin-right: 6px;
    border-radius: 100px;
    background: variables.$yellow;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: variables.$black;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    width: 100%;
    border: none;
    position: relative;
    transition: all .25s ease-in-out;
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 100px;
      transition: all .3s ease-in-out;
      background: linear-gradient(275deg, #FFD233 0.95%, #FEEC49 47.84%, #FFD233 98.64%);
      opacity: 0;
    }
    span {
      position: relative;
      z-index: 1;
    }
    @media all and (min-width: 1250px) {
        &:hover {
          background: #f3f4f6;
          &::after {
            opacity: 1;
          }
        }
    }
  }
}
.sign-up-btn,
.sign-in-btn {
  margin-left: 5px;
  color: variables.$grey;
  text-decoration: underline;
  @media (min-width: 1250px) {
    &:hover {
      color: variables.$grey;
      text-decoration: none;
    }
  }
}
.eye-1 {
  display: block;
}
.eye-2 {
  display: none;
}
div,
article {
    &.comics {
        border-radius: 12px;
        position: relative;
        vertical-align: top;
        .comic {
            &-thumbnail {
                min-height: 148px;
                position: relative;
                overflow: hidden;
                border-radius: 12px;
                &::after {
                  content: '';
                  position: absolute;
                  bottom: 0;
                  height: 12px;
                  left: 0;
                  border-bottom: 2px solid variables.$yellow;
                  opacity: 0;
                  transition: all 1s ease-in-out;
                  z-index: 1;
                  border-radius: 0 0 12px 12px;
                }
                @media (min-width: 1250px) {
                  &:hover {
                    img {
                      transform: scale(1.05);
                    }
                    &::after {
                      right: 0;
                      opacity: 1;
                    }
                  }
                }
                .labels,
                .episodes,
                .views {
                    position: absolute;
                    z-index: 1;
                }

                .views,
                .labels {
                    padding: 4px;
                }

                .views,
                .episodes {
                    bottom: 0;
                }

                .episodes,
                .views>span {
                    display: inline-flex;
                    background: variables.$views;
                    svg {
                      display: block;
                      transform: translateZ(0);
                    }
                }
                .views>span {
                  height: 24px;
                  line-height: 24px;
                }
                .episodes {
                  width: 24px;
                  height: 22px;
                  justify-content: center;
                  align-items: center;
                  backdrop-filter: blur(14px);
                }
                .label {
                    color: variables.$white;
                    padding: 0 6px;
                    font-size: 12px;
                    line-height: 2;
                    font-weight: 500;
                    border-radius: 100px;
                    height: 24px;
                    &s {
                        top: 0;
                        left: 0;
                        display: flex;
                        gap: 5px;
                        align-items: center;
                    }

                    &.adult {
                        background: linear-gradient(to right, variables.$soft-red 3%, variables.$red 95%);
                        padding: 5px 7.5px;
                        font-size: 12px;
                        line-height: 1.25;
                    }

                    &.end {
                        background: linear-gradient(to right, variables.$soft-purple 3%, variables.$medium-purple 95%);
                    }
                }

                .views {
                    left: 0;

                    &>span {
                        color: variables.$white;
                        border-radius: 100px;
                        padding: 4px 6px;
                        align-items: center;
                        gap: 2px;
                        font-size: 12px;
                        backdrop-filter: blur(14px);
                    }
                }

                .episodes {
                    right: 0;
                    color: variables.$yellow;
                    border-radius: 10px 0;
                    font-size: 12px;
                }

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    object-position: center;
                    border-radius: 12px;
                    transition: all 0.6s ease-in-out;
                    z-index: 0;
                }
            }

            &-title {
                color: variables.$black;
                font-weight: 500;
                margin: 0;
                font-size: 14px;
                line-height: 130%;
                height: 34px;
                overflow: hidden;
                text-overflow: ellipsis;
                line-clamp: 2;
                -webkit-line-clamp: 2;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                @media (max-width: 767px) {
                  font-size: 12px;
                  font-weight: 500;
                  line-height: 130%;
                  height: 32px;
                }
            }

            &-content {
                padding: 6px 0 0;
            }

            &-genre {
                font-size: 12px;
                font-weight: 500;
                display: block;
                line-height: 150%;
                padding-bottom: 2px;
                @media (max-width: 767px) {
                  font-size: 10px;
                  line-height: 140%;
                }
            }
        }
    }

    &.banner {
        min-height: 300px;
        border-radius: 20px;
        font-size: 14px;
        line-height: 20px;

        .content {
            padding: 22px 16px 13px;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            display: flex;
            flex-direction: column;
            gap: 16px;

            &.center {
                justify-content: center;
            }

            &.bottom {
                justify-content: flex-end;
            }

            &.between {
                justify-content: space-between;
            }
        }

        img {
            border-radius: 20px;
            width: 100%;
        }
    }
}
.see-all {
  transition: all .3s ease-in-out;
  color: variables.$grey;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  gap: 5px;
  font-size: 14px;
  margin-bottom: 8px;
  svg {
    margin-top: -1px;
  }
  @media (min-width: 768px) {
      font-size: 16px;
      margin-bottom: 16px;
  }
  span {
    position: relative;
    display: block;
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 0;
      background: #010101;
      height: 1px;
      transition: all .3s ease-in-out;
    }
  }
  path {
    transition: all .3s ease-in-out;
    stroke: #4c4c4c;
  }
  @media (min-width: 1250px) {
    &:hover {
      color: variables.$black;
      span {
        &::after {
          width: 100%;
        }
        svg path {
          stroke: #010101;
        }
      }
    }
  }
}
.content-page {
  color: #0d0c0c;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  font-family: variables.$secondary-font;
  h1 {
    color: variables.$black;
    font-size: 44px;
    font-style: normal;
    font-weight: 700;
    line-height: 105%;
    margin: 0 0 16px;
    font-family: 'Lufga';
  }
  h2 {
    color: variables.$black;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin: 0 0 10px;
    font-family: 'Lufga';
  }
  h3 {
    font-family: 'Lufga';
    font-size: 18px;
    font-weight: 500;
  }
  a {
    color: variables.$black;
    text-decoration: underline;
    @media (min-width: 1250px) {
      &:hover {
        color: #febc49;
      }
    }
  }
  p {
    margin: 10px 0;
  }
  ul {
    margin: 10px 0;
    li + li {
      margin-top: 10px;
    }
  }
}
.breadcrumbs {
  margin: 0 auto 10px;
  padding: 0;
  list-style: none;
  color: #9d9aa2;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  max-width: 1110px;
  @media (max-width: 1250px) {
    padding: 0 16px;
  }
  ol {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  }
  li {
    display: flex;
    & + li {
      &::before {
        content: '/';
        color: #9d9aa2;
        margin: 0 4px;
      }
    }
  }
  span {
    color: #9d9aa2;
  }
  a {
    color: variables.$grey;
  }
}
.error-page {
  height: 100vh;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  background-position: 50% 0;
  text-align: center;
  &__subtitle {
    color: #fff;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
  }
  &__title {
    color: #fff;
    font-size: 180px;
    font-style: normal;
    font-weight: 700;
    line-height: 105%;
  }
  &__text {
    color: #fff;
    text-align: center;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    max-width: 330px;
    margin: 0 auto 32px;
  }
  &__buttons {
    display: flex;
    justify-content: center;
  }
  &__btn {
    border-radius: 100px;
    background: variables.$yellow;
    max-width: 172px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 44px;
    color: variables.$black;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 100px;
      transition: all .3s ease-in-out;
      background: linear-gradient(275deg, #FFD233 0.95%, #FEEC49 47.84%, #FFD233 98.64%);
      opacity: 0;
    }
    span {
      position: relative;
      z-index: 1;
    }
    @media all and (min-width: 1250px) {
        &:hover {
          background: #f3f4f6;
          color: variables.$black;
          background: variables.$yellow;
          &::after {
            opacity: 1;
          }
        }
    }
  }
}
