@use 'utilities/variables';
@use 'utilities/mixins';
.account-holder {
  display: flex;
  &__tab {
    display: none;
    @media (max-width: 767px) {
      position: absolute;
      left: 0;
      top: 0;
      transform: translateX(120%);
      transition: all .3s ease-in-out;
      display: block;
      width: 100%;
      padding: 76px 15px 0;
      background: #fcfbfb;
      z-index: 1;
    }
    &.active {
      display: block;
      @media (max-width: 767px) {
        transform: translateX(0%);
      }
    }
  }
  &__title {
    color: variables.$black;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    margin-bottom: 20px;
    position: relative;
    @media (max-width: 767px) {
      padding-left: 18px;
      &::after {
        border-bottom: 1px solid #010101;
        border-right: 1px solid #010101;
        content: '';
        display: block;
        width: 5.5px;
        height: 5.5px;
        left: 0;
        margin-top: -4px;
        pointer-events: none;
        position: absolute;
        top: 50%;
        -webkit-transform-origin: 66% 66%;
        transform-origin: 66% 66%;
        -webkit-transform: rotate(135deg);
        transform: rotate(135deg);
        -webkit-transition: all 0.15s ease-in-out;
        transition: all 0.15s ease-in-out;
      }
    }
  }
  &__content {
    width: 100%;
    margin-left: 10px;
    padding: 36px 40px;
    background: #fff;
    border-radius: 20px;
    @media (max-width: 767px) {
      background: none;
    }
  }
  &__aside {
    padding: 0;
    max-width: 365px;
    width: 100%;
    flex-shrink: 0;
    @media (max-width: 767px) {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      max-width: 100%;
      height: 70vh;
    }
  }
  &__aside-nav {
    @media (min-width: 768px) {
      padding: 32px 24px;
      background: #fff;
      border-radius: 16px;
      box-shadow: 0px 1px 30px 0px rgba(61, 61, 61, 0.08);
      margin-bottom: 20px;
    }
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
    }
    li {
      & + li {
        margin-top: 10px;
        @media (max-width: 767px) {
          margin-top: 8px;
        }
      }
      @media (min-width: 768px) {
        &.active {
          a {
            background: #202020;
            color: #fff;
            svg {
              fill: #fff;
            }
            &::after {
              border-bottom: 1px solid #fff;
              border-right: 1px solid #fff;
            }
          }
        }
      }
    }
    a {
      display: flex;
      align-items: center;
      padding: 14px 20px 14px 0;
      color: variables.$black;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
      text-decoration: none;
      position: relative;
      transition: all .3s ease-in-out;
      @media (min-width: 768px) {
        border-radius: 100px;
        background: #f3f4f6;
        padding: 10px 20px;
      }
      @media (max-width: 767px) {
        &:active {
          opacity: 0.7;
        }
      }
      &::after {
        border-bottom: 1px solid #010101;
        border-right: 1px solid #010101;
        content: '';
        display: block;
        width: 5.5px;
        height: 5.5px;
        right: 20px;
        margin-top: -4px;
        pointer-events: none;
        position: absolute;
        top: 50%;
        -webkit-transform-origin: 66% 66%;
        transform-origin: 66% 66%;
        -webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
        -webkit-transition: all 0.15s ease-in-out;
        transition: all 0.15s ease-in-out;
        @media (max-width: 767px) {
          right: 0;
        }
      }
      @media (min-width: 1250px) {
        &:hover {
            background: variables.$yellow;
            color: variables.$black;
        }
      }
      svg {
        margin-right: 12px;
        fill: #010101;
      }
    }
  }
  &__exit {
    display: flex;
    color: #e90000;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    align-items: center;
    text-decoration: none;
    @media (min-width: 1250px) {
      &:hover {
          text-decoration: underline;
          color: #e90000;
      }
    }
    svg {
      margin-right: 12px;
    }
  }
}
