.account-holder {
  display: flex;
}
.account-holder__tab {
  display: none;
}
@media (max-width: 767px) {
  .account-holder__tab {
    position: absolute;
    left: 0;
    top: 0;
    transform: translateX(120%);
    transition: all 0.3s ease-in-out;
    display: block;
    width: 100%;
    padding: 76px 15px 0;
    background: #fcfbfb;
    z-index: 1;
  }
}
.account-holder__tab.active {
  display: block;
}
@media (max-width: 767px) {
  .account-holder__tab.active {
    transform: translateX(0%);
  }
}
.account-holder__title {
  color: #010101;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  margin-bottom: 20px;
  position: relative;
}
@media (max-width: 767px) {
  .account-holder__title {
    padding-left: 18px;
  }
  .account-holder__title::after {
    border-bottom: 1px solid #010101;
    border-right: 1px solid #010101;
    content: "";
    display: block;
    width: 5.5px;
    height: 5.5px;
    left: 0;
    margin-top: -4px;
    pointer-events: none;
    position: absolute;
    top: 50%;
    -webkit-transform-origin: 66% 66%;
    transform-origin: 66% 66%;
    -webkit-transform: rotate(135deg);
    transform: rotate(135deg);
    -webkit-transition: all 0.15s ease-in-out;
    transition: all 0.15s ease-in-out;
  }
}
.account-holder__content {
  width: 100%;
  margin-left: 10px;
  padding: 36px 40px;
  background: #fff;
  border-radius: 20px;
}
