  .account-holder__content {
    background: none;
  }
}
.account-holder__aside {
  padding: 0;
  max-width: 365px;
  width: 100%;
  flex-shrink: 0;
}
@media (max-width: 767px) {
  .account-holder__aside {
    display: flex;
    justify-content: space-between;
