  }
  .account-holder__aside-nav li.active a svg {
    fill: #fff;
  }
  .account-holder__aside-nav li.active a::after {
    border-bottom: 1px solid #fff;
    border-right: 1px solid #fff;
  }
}
.account-holder__aside-nav a {
  display: flex;
  align-items: center;
  padding: 14px 20px 14px 0;
  color: #010101;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  text-decoration: none;
  position: relative;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .account-holder__aside-nav a {
    border-radius: 100px;
    background: #f3f4f6;
    padding: 10px 20px;
  }
}
@media (max-width: 767px) {
  .account-holder__aside-nav a:active {
    opacity: 0.7;
  }
}
.account-holder__aside-nav a::after {
  border-bottom: 1px solid #010101;
  border-right: 1px solid #010101;
  content: "";
  display: block;
  width: 5.5px;
  height: 5.5px;
  right: 20px;
  margin-top: -4px;
  pointer-events: none;
  position: absolute;
  top: 50%;
  -webkit-transform-origin: 66% 66%;
  transform-origin: 66% 66%;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -webkit-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
}
@media (max-width: 767px) {
  .account-holder__aside-nav a::after {
    right: 0;
  }
}
@media (min-width: 1250px) {
  .account-holder__aside-nav a:hover {
    background: #FEEC49;
