  }
}
.account-holder__aside-nav a svg {
  margin-right: 12px;
  fill: #010101;
}
.account-holder__exit {
  display: flex;
  color: #e90000;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
