@use "utilities/variables.scss";
@use "utilities/mixins.scss";
.forgot-page {
  main {
    padding-top: 66px;
    @media (max-width: 767px) {
      padding-top: 52px;
    }
  }
}
.forgot-section {
  position: relative;
  height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1142px;
  padding: 0 16px;
  margin: 0 auto;
  .episode-inner__back {
    position: absolute;
    top: 20px;
    left: 0;
    color: variables.$black;
    width: auto;
    padding: 0 7px;
    @media (max-width: 1250px) {
      left: 10px;
    }
    svg {
      stroke: #010101;
      margin-right: 7px;
    }
    @media (min-width: 1250px) {
      &:hover {
          background: variables.$yellow;
      }
    }
  }
  &__title {
    color: variables.$black;
    text-align: center;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin-bottom: 14px;
    @media (max-width: 479px) {
      text-align: left;
    }
  }
  &__text {
    color: variables.$black;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    max-width: 370px;
    width: 100%;
    margin: 0 auto 24px;
    @media (max-width: 479px) {
      text-align: left;
      margin: 0 0 24px;
    }
  }
  &__box {
    max-width: 456px;
    width: 100%;
    .modal__row {
      margin-bottom: 24px;
    }
  }
}
