@use "utilities/variables.scss";
@use "utilities/mixins.scss";
.payment-section {
  @media (max-width: 767px) {
    padding-top: 13px;
  }
  &__title {
    color: variables.$black;
    font-size: 44px;
    font-weight: 700;
    line-height: 105%;
    margin: 0 0 24px;
    text-align: center;
    @media (max-width: 767px) {
      font-size: 24px;
      margin-bottom: 18px;
    }
  }
  &__holder {
    margin-bottom: 34px;
    @media (max-width: 767px) {
      margin-bottom: 24px;
    }
  }
  &__list {
    display: flex;
    margin: -20px 0 24px -20px;
    @media (max-width: 1023px) {
      flex-wrap: wrap;
      margin: -6px 0 18px;
    }
  }
  &__item {
    max-width: 262px;
    width: 100%;
    margin: 20px 0 0 20px;
    @media (max-width: 1023px) {
      max-width: 47%;
    }
    @media (max-width: 767px) {
      max-width: 100%;
      margin: 6px 0 0;
    }
    input {
      position: absolute;
      left: -9999px;
      &:checked {
        & + .payment-section__box {
          background: #f5fffc;
          border: 1px solid #38aa7e;
        }
      }
    }
  }
  &__box {
    border-radius: 12px;
    border: 1px solid #fff;
    border-bottom-color: #f3f4f6;
    background: #fff;
    box-shadow: 0px 1px 30px 0px rgba(61, 61, 61, 0.08);
    display: block;
    padding: 20px;
    height: 100%;
    @media (min-width: 1250px) {
      &:hover {
        border: 1px solid #38aa7e;
      }
    }
    @media (max-width: 767px) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 12px 10px;
    }
  }
  &__prices-box {
    @media (max-width: 767px) {
      order: 1;
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-end;
      text-align: right;
      max-width: 70px;
      width: 100%;
    }
  }
  &__ico-desct {
    @media (max-width: 767px) {
      display: none;
    }
  }
  &__ico-mob {
    display: none;
    @media (max-width: 767px) {
      display: block;
    }
  }
  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 767px) {
      flex-shrink: 0;
      flex-wrap: wrap;
      max-width: 88px;
      width: 100%;
    }
  }
  &__week {
    color: variables.$black;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    @media (max-width: 767px) {
      font-size: 17px;
      order: 1;
      width: 100%;
    }
  }
  &__pop {
    display: flex;
    align-items: center;
    color: #fff;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    @media (max-width: 767px) {
      font-size: 10px;
      margin-bottom: 3px;
    }
    span {
      border-radius: 6px;
      background: #fc615d;
      height: 22px;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0 6px;
      @media (max-width: 767px) {
        height: 18px;
        padding: 0 4px;
        border-radius: 4px;
      }
    }
    svg {
      margin-left: 2px;
      flex-shrink: 0;
    }
  }
  &__prices {
    display: flex;
    align-items: flex-end;
    margin-bottom: 20px;
    @media (max-width: 767px) {
      order: 1;
      width: 100%;
      display: block;
      margin: 0;
    }
    sub {
      margin-left: 1px;
      color: variables.$grey;
      font-size: 12px;
      font-style: normal;
      font-weight: 500;
      line-height: 42px;
      &::before {
        content: '/';
        @media (max-width: 767px) {
          display: none;
        }
      }
      @media (max-width: 767px) {
        font-size: 10px;
        line-height: 12px;
        display: block;
        margin: -5px 0 0;
      }
    }
  }
  &__price {
    color: variables.$black;
    font-size: 28px;
    font-weight: 500;
    line-height: 42px;
    @media (max-width: 767px) {
      font-size: 18px;
      line-height: 20px;
    }
  }
  &__sale {
    border-radius: 5px;
    background: #f3f4f6;
    color: variables.$grey;
    font-size: 10px;
    font-weight: 500;
    line-height: 140%;
    height: 16px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0 4px;
    margin-bottom: 6px;
    @media (max-width: 767px) {
      margin-bottom: 4px;
    }
    &.sale {
      color: #fff;
      background: #38aa7e;
    }
  }
  &__text {
    color: variables.$grey;
    font-family: 'Manrope';
    font-size: 14px;
    font-weight: 500;
    line-height: 150%;
    display: block;
    @media (max-width: 767px) {
      max-width: 162px;
      font-size: 11px;
    }
    em {
      font-style: normal;
      text-decoration: line-through;
    }
  }
  &__buttons {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
    @media (max-width: 767px) {
      margin-bottom: 18px;
    }
  }
  &__btn-1 {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
    background: #010101;
    height: 44px;
    cursor: pointer;
    border: none;
    max-width: 550px;
    width: 100%;
    svg {
      transition: all .3s ease-in-out;
      fill: #fff;
    }
    @media (min-width: 1250px) {
      &:hover {
        background: #010101;
        svg {
          fill: variables.$yellow;
        }
      }
    }
  }
  &__btn-2 {
    color: variables.$black;
    font-size: 14px;
    font-weight: 500;
    border-radius: 100px;
    line-height: 140%;
    height: 44px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: variables.$yellow;
    cursor: pointer;
    border: none;
    max-width: 550px;
    width: 100%;
    margin-left: 10px;
    position: relative;
    span {
      position: relative;
      z-index: 1;
    }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 100px;
      background: linear-gradient(275deg, #FFD233 0.95%, #FEEC49 47.84%, #FFD233 98.64%);
      opacity: 0;
      transition: all .3s ease-in-out;
    }
    @media (min-width: 1250px) {
      &:hover {
        &::before {
          opacity: 1;
        }
      }
    }
  }
  &__check {
    input {
      position: absolute;
      left: -9999px;
      &:checked {
        & + span {
          &::after {
            background: #0f0e0e;
          }
          &::before {
            opacity: 1;
          }
        }
      }
    }
    span {
      color: variables.$grey;
      font-family: 'Manrope';
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: 150%;
      position: relative;
      display: flex;
      padding-left: 34px;
      @media (max-width: 767px) {
        font-size: 12px;
        max-width: 330px;
      }
      &::after {
        content: '';
        position: absolute;
        left: 0;
        width: 22px;
        height: 22px;
        border: 1px solid #0f0e0e;
        border-radius: 4px;
        @media (max-width: 767px) {
          top: 7px;
        }
      }
      &::before {
        content: '';
        position: absolute;
        border: 1px solid #fff;
        border-width: 0 0 2px 2px;
        width: 11px;
        height: 6px;
        left: 6px;
        top: 50%;
        margin-top: -4px;
        -webkit-transform: scale(1) rotate(-45deg);
        transform: scale(1) rotate(-45deg);
        z-index: 1;
        opacity: 0;
      }
    }
  }
  &__info-text {
    color: #0d0c0c;
    font-family: 'Manrope';
    font-size: 14px;
    font-weight: 500;
    line-height: 150%;
    margin-bottom: 32px;
    @media (max-width: 767px) {
      font-size: 12px;
    }
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      li {
        padding-left: 22px;
        position: relative;
        & + li {
          margin-top: 8px;
        }
        &::after {
          content: '';
          position: absolute;
          left: 9px;
          width: 4px;
          height: 4px;
          border-radius: 50%;
          background: #0d0c0c;
          top: 9px;
        }
      }
      a {
        color: #0d0c0c;
        text-decoration: underline;
        @media (min-width: 1250px) {
          &:hover {
            color: #febc49;
          }
        }
      }
    }
  }
  &__method {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    li {
      & + li {
        margin-left: 18px;
      }
    }
  }
}
