@use 'utilities/variables';
@use 'utilities/mixins';
@use 'parts/header';
@use 'parts/general';
@use 'parts/faq';
@use 'parts/footer';
@use 'parts/forms';
@use 'parts/modals';

.page-template-default {
    h1, h2, h3, h4, h5, h6 {
        margin-bottom: 8px;
    }
    p {
        margin-bottom: 16px;
    }
    ol, ul {
        margin-bottom: 25px;
        padding-left: 15px;
    }
}

.content-page  {
    color: #0d0c0c;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    font-family: variables.$secondary-font;

    h1, h2, h3, h4, h5, h6 {
        font-family: variables.$primary-font;
    }

    a {

        color: #010101;
        text-decoration: underline;
    }
}
