@use '../utilities/variables';
@use '../utilities/mixins';
.social {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  li {
    & + li {
      margin-left: 8px;
    }
  }
  a {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    background: #efefef;
    transition: all .3s ease-in-out;
    svg {
      fill: #010101;
      transition: all .3s ease-in-out;
    }
    @media (min-width: 1250px) {
      &:hover {
        svg {
          fill: #febc49;
        }
      }
    }
  }
}
.footer {
    background: variables.$black;
    color: variables.$white;
    font-size: 16px;
    line-height: 1.25;
    &-content {
        border-bottom: 1px solid #202020;
        padding-bottom: 56px;
        @media (min-width: 768px) {
          display: flex;
          justify-content: space-between;
        }
        @media (max-width: 767px) {
          padding-bottom: 44px;
        }
        .menu {
            list-style: none;
            padding: 0;
            margin: 0;
            li {
              & + li {
                margin-top: 12px;
              }
            }
            a {
                color: variables.$white;
                font-weight: 400;
                font-size: 14px;
                cursor: pointer;
                @media (max-width: 767px) {
                  font-size: 16px;
                }
                &:hover {
                    color: variables.$yellow;
                }
            }
        }
        &__navigation {
          display: flex;
          @media (min-width: 768px) {
            justify-content: flex-end;
            align-items: flex-start;
            gap: 100px;
            max-width: 644px;
            width: 100%;
          }
          @media (max-width: 1023px) {
            gap: 60px;
          }
          @media (max-width: 767px) {
            gap: 0;
            flex-wrap: wrap;
            .menu-container {
              width: 100%;
              order: 1;
            }
          }
        }
        .social {
          margin-top: 24px;
          @media (max-width: 767px) {
            margin-bottom: 36px;
          }
          a {
            background: rgba(255, 255, 255, 0.10);
            @media (max-width: 767px) {
              border-radius: 8px;
            }
            svg {
              fill: #fff;
            }
            @media (min-width: 1250px) {
              &:hover {
                svg {
                  fill: variables.$yellow;
                }
              }
            }
          }
        }
    }
    hr {
        margin: 24px 0;
    }
    &-mobile {
        padding: 48px 16px;
        .menu {
            flex-direction: column;
            gap: 10px;
        }
        hr {
            border-color:#181818;
        }
        .footer-copyright {
            font-size: 12px;
            color: #999999;
        }
    }
    &-desktop {
        padding: 60px 0 30px;
        .menu {
            gap: 24px;
        }
    }
}
.footer-bottom {
  padding-top: 64px;
  @media (max-width: 767px) {
    padding-top: 44px;
  }
  &__company {
    color: rgba(255,255,255,0.8);
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin-bottom: 24px;
    font-family: 'Manrope';
    @media (min-width: 768px) {
      display: flex;
    }
    p {
      margin: 0;
    }
  }
  &__company-item {
    @media (min-width: 768px) {
      max-width: 343px;
      width: 100%;
    }
    & + .footer-bottom__company-item {
      margin-left: 54px;
      @media (max-width: 767px) {
        margin: 14px 0 0;
      }
    }
  }
}
.footer-copyright {
  color: rgba(255,255,255,0.8);
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  font-family: 'Manrope';
  line-height: 150%;
}
@media (max-width: 767px) {
  .menu-container + .menu-container {
    margin-top: 36px;
    a {
      font-size: 14px;
    }
  }
}

.menu-container + .menu-container a {
  color: rgba(255,255,255,0.8);
}
