@use '../utilities/variables';
@use '../utilities/mixins';
:root {
    --swiper-pagination-bottom: 8px;
}
.swiper-container {
  @media (max-width: 767px) {
    padding-right: 24px;
  }
}
.slide-content-top {
  display: flex;
  align-items: center;
  margin-bottom: 6px;
  @media (min-width: 992px) {
      margin-bottom: 30px;
  }
  @media (max-width: 767px) {
    justify-content: space-between;
  }
  .views {
    color: #fff;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    border-radius: 100px;
    background: rgba(0, 0, 0, 0.30);
    backdrop-filter: blur(14px);
    height: 23px;
    padding: 0 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 4px;
    span {
      display: flex;
      align-items: center;
      justify-content: center;
      svg {
        margin-right: 2px;
      }
    }
  }
}
.section-slider {
    .container {
        position: relative;
        z-index: 1;
    }
    @media (min-width: 768px) {
        padding: 0 0 20px;
        .container {
            overflow: hidden;
        }
    }

    .swiper {
        &-container {
            position: relative;
        }

        &-pagination {
            @media (max-width: 767px) {
              bottom: -21px;
            }
            &-bullet {
                border: none;
                background-color: rgba(255,255,255,0.6);
                transition: all .15s ease-in-out;
                border-radius: 50px;
                opacity: 1;
                height: 6px;
                width: 6px;
                @media (max-width: 767px) {
                  bottom: -21px;
                  background-color: rgba(214,215,224,0.6);
                }
                &-active {
                    width: 24px;
                    background: variables.$white;
                    @media (max-width: 767px) {
                      background: #010101;
                    }
                }
            }
        }
    }

    .slide {
        position: relative;
        @media (max-width: 767px) {
          margin-right: 8px;
        }
        img {
            width: 100%;
            height: 735px;
            object-fit: cover;
            @media (max-width: 1700px) {
              height: 560px;
            }
            @media (max-width: 767px) {
              border-radius: 20px;
              height: 430px;
            }
        }

        &-content {
            position: absolute;
            width: 100%;
            top: 0;
            bottom: 0;
            left: 0;
            display: flex;
            align-items: center;
            padding: 0 16px;
            &::after {
              content: '';
              position: absolute;
              background: linear-gradient(90deg, #1B1223 0%, rgba(0, 0, 0, 0.00) 100%);
              top: 0;
              bottom: 0;
              left: 0;
              width: 940px;
              @media (max-width: 1440px) {
                width: 560px;
              }
              @media (max-width: 767px) {
                height: 160px;
                top: auto;
                width: 100%;
                background: linear-gradient(0deg, #1B1223 0%, rgba(0, 0, 0, 0.00) 100%);
                border-radius: 0 0 20px 20px;
              }
            }
            @media (max-width: 767px) {
              align-items: flex-end;
              margin: 0;
              .container {
                margin: 0;
              }
            }
            .button {
              @media (min-width: 768px) {
                font-size: 14px;
                height: 40px;
                min-height: 40px;
                display: inline-flex;
                align-items: center;
                justify-content: flex-start;
                border-radius: 100px;
                background: variables.$yellow;
                color: variables.$black;
                font-size: 14px;
                font-style: normal;
                font-weight: 500;
                line-height: 140%;
                padding: 0 40px 0 20px;
                min-width: 223px;
                position: relative;
              }
              span {
                position: relative;
                z-index: 1;
              }
              &::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                border-radius: 100px;
                background: linear-gradient(275deg, #FFD233 0.95%, #FEEC49 47.84%, #FFD233 98.64%);
                opacity: 0;
                transition: all .3s ease-in-out;
              }
              &::after {
                border-bottom: 1px solid #010101;
                border-right: 1px solid #010101;
                content: '';
                display: block;
                width: 5.5px;
                height: 5.5px;
                right: 20px;
                margin-top: -4px;
                pointer-events: none;
                position: absolute;
                top: 50%;
                -webkit-transform-origin: 66% 66%;
                transform-origin: 66% 66%;
                -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
                -webkit-transition: all 0.15s ease-in-out;
                transition: all 0.15s ease-in-out;
                z-index: 1;
              }
              @media (min-width: 1250px) {
                &:hover {
                  &::before {
                    opacity: 1;
                  }
                }
              }
          }

            .genre {
                &__link {
                    color: variables.$white;
                    padding: 2px 8px;
                    border-radius: 8px 0;
                    font-size: 13px;
                    line-height: 140%;
                }
                &s {
                    display: flex;
                    gap: 4px;
                    @media (max-width: 767px) {
                      padding-bottom: 0;
                    }
                }
            }
        }

        &-title {
            font-size: 24px;
            line-height: 1.35;
            margin: 0 0 2px;
            font-weight: 700;
            @media (min-width: 768px) {
                font-size: 44px;
                line-height: 1.05;
                margin: 0 0 12px;
                font-weight: 700;
                max-width: 400px;
                max-width: 282px;
            }
        }

        &-excerpt {
            font-size: 12px;
            line-height: 150%;
            width: 100%;
            max-width: 306px;
            margin: 0 0 30px;
            overflow: hidden;
            text-overflow: ellipsis;
            line-clamp: 4;
            -webkit-line-clamp: 4;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            font-family: 'Manrope';
            font-weight: 500;
            @media (max-width: 767px) {
              line-clamp: 2;
              -webkit-line-clamp: 2;
            }
        }

    }
}
.swiper-button-prev {
  width: 42px;
  height: 42px;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  border-radius: 100%;
  border: unset;
  background: #F3F3F3;
  position: absolute;
  top: calc(50% - 21px);
  cursor: pointer;
  z-index: 2;
  left: 40px;
  transition: all .3s ease-in-out;
  @media (max-width: 1250px) {
    display: none;
  }
}
.swiper-button-next {
  width: 42px;
  height: 42px;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  border-radius: 100%;
  border: unset;
  background: #F3F3F3;
  position: absolute;
  top: calc(50% - 21px);
  cursor: pointer;
  transition: all .3s ease-in-out;
  z-index: 2;
  right: 40px;
  @media (max-width: 1250px) {
    display: none;
  }
}
@media (min-width: 1250px) {
  .swiper-button-prev,
  .swiper-button-next {
    &:hover {
      background: variables.$yellow;
    }
  }
}
.swiper-button-next:after, .swiper-button-prev:after {
  display: none;
}
.swiper-button-next svg, .swiper-button-prev svg {
  width: auto;
  height: auto;
}
