@use '../utilities/variables';
@use '../utilities/mixins';
.header {
    background: variables.$dark;
    font-size: 13px;
    width: 100%;
    position: fixed;
    z-index: 20;
    a {
        color: variables.$white;
    }

    &-mobile {
        padding: 10px 0;
        min-height: 52px;
    }

    &-desktop {
        padding: 15px 0;
        min-height: 66px;
    }
    @media (max-width: 767px) {
      min-height: 52px;
      padding: 10px 0;
    }
    .row,
    .buttons,
    .button {
        display: flex;
        align-items: center;
    }

    .row {
        &.header {
            &-main {
                justify-content: space-between;
            }

            &-bottom {
                margin-top: 15px;
                justify-content: space-around;
            }
        }
    }
    &__sign {
      display: none;
      @media (max-width: 767px) {
        display: block;
        border-radius: 100px;
        border: 1px solid #fff;
        height: 44px;
        background: none;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        text-align: center;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 140%;
        margin: 0 16px;
        svg {
          margin-right: 9px;
        }
      }
    }
    .adult-switcher {
        position: relative;
        width: 64px;
        height: 32px;
        min-height: 32px;
        margin-right: 20px;
        border-radius: 8px;
        border: 1px solid #4c4c4c;
        padding: 1px;
        background: none;
        cursor: pointer;
        @media (max-width: 767px) {
          margin-right: 8px;
        }
        .square {
            display: flex;
            width: 28px;
            height: 28px;
            background: #9d9aa2;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 1px;
            left: 1px;
            transition: all .25s ease-in-out;
            border-radius: 6px;
            color: #fff;
            font-size: 14px;
            font-style: normal;
            font-weight: 500;
            font-family: "Lufga", sans-serif;
            line-height: 140%;
        }

        &.active {
          border-color: variables.$yellow;
          background: rgba(254, 236, 73, 0.10);
          .square {
            background: variables.$yellow;
            left: calc(100% - 29px);
            color: variables.$black;
          }
        }
    }

    .open-menu {
        width: 32px;
        background: variables.$dark-button;
        padding: 0;
    }

    .menu {
        display: flex;
        gap: 13px;
        list-style: none;
        align-items: center;
        padding: 0;
        margin: 0;
        @media (max-width: 767px) {
          display: block;
        }
        &-general {
          li.current_page_item {
            a {
              color: variables.$yellow;
            }
          }
            a {
                line-height: 1;
                display: flex;
                align-items: center;
                gap: 5px;
                transition: all .3s ease-in-out;
                @media (min-width: 1250px) {
                  &:hover {
                    color: variables.$yellow;
                  }
                }
                @media (max-width: 767px) {
                  padding: 14px 16px;
                  font-size: 16px;
                  position: relative;
                  &::after {
                    border-bottom: 1px solid #9d9aa2;
                    border-right: 1px solid #9d9aa2;
                    content: '';
                    display: block;
                    width: 6px;
                    height: 6px;
                    right: 18px;
                    margin-top: -4px;
                    pointer-events: none;
                    position: absolute;
                    top: 50%;
                    transform-origin: 66% 66%;
                    transform: rotate(-45deg);
                    -webkit-transition: all 0.15s ease-in-out;
                    transition: all 0.15s ease-in-out;
                  }
                }
            }
        }

        @media (min-width: 992px) {
            gap: 32px;
        }
    }
}
.site-url {
  @media (max-width: 767px) {
      max-width: 85px;
      display: block;
      line-height: 14px;
  }
}
.auth {
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.10);
  backdrop-filter: blur(12px);
  display: flex;
  align-items: center;
  color: #fff;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  height: 32px;
  padding: 0 12px;
  position: relative;
  transition: all .3s ease-in-out;
  @media (max-width: 767px) {
    display: none;
  }
  @media (min-width: 1250px) {
    &:hover {
      color: variables.$yellow;
      svg {
        fill: variables.$yellow;
      }
    }
  }
  svg {
    margin-left: 4px;
    fill: #fff;
    transition: all .3s ease-in-out;
  }
  &--logined {
    &::after {
      content: '';
      position: absolute;
      width: 7px;
      height: 7px;
      border-radius: 6px;
      border: 1px solid #fff;
      background: #65Af4b;
      bottom: 6px;
      right: 11px;
    }
  }
}
.header-holder {
  @media (max-width: 767px) {
    position: fixed;
    transform: translateX(100%);
    top: 52px;
    height: calc(100vh - 52px);
    background: #010101;
    width: 100%;
    padding: 66px 0 56px;
    z-index: 10;
    right: 0;
    display: flex;
    flex-direction: column;
    transition: all .3s ease-in-out;
    justify-content: space-between;
    &.show {
      transform: translateX(0%);
    }
  }
}
.mobile-opener {
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.10);
  width: 81px;
  height: 32px;
  min-height: 32px;
  padding: 0;
  border: none;
  position: relative;
  margin-left: 8px;
  color: #fff;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  padding-right: 10px;
  align-items: center;
  justify-content: flex-end;
  display: none;
  @media (max-width: 767px) {
    display: flex;
  }
  &__line {
    background: #fff;
    height: 1px;
    position: absolute;
    left: 10px;
    &--1 {
      width: 9px;
      top: 9px;
      left: 19px;
    }
    &--2 {
      width: 18px;
      top: 15px;
    }
    &--3 {
      width: 14px;
      left: 14px;
      top: 21px;
    }
  }
  &.active {
    .mobile-opener__line--1 {
      display: none;
    }
    .mobile-opener__line--2 {
      width: 18px;
      transform: rotate(45deg);
    }
    .mobile-opener__line--3 {
      width: 18px;
      top: 15px;
      left: 10px;
      transform: rotate(-45deg);
    }
  }
}
