@use '../utilities/variables';
@use '../utilities/mixins';
.loadmore {
    padding: 10px 28px;
    border: 1px solid variables.$black;
    font-size: 14px;
    border-radius: 30px;
    background: transparent;
    cursor: pointer;
    font-weight: 500;
    font-family: 'Lufga';
    transition: all .3s ease-in-out;
    @media (min-width: 1250px) {
      &:hover {
          border-color: variables.$yellow;
      }
    }


    &.d-none {
        display: none;
        & + img {
          display: block;
        }
    }

    @media (min-width: 768px) {
        padding: 12px 32px;
    }

    &-wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        margin-bottom: 60px;
        @media (max-width: 767px) {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
        img {
          display: none;
        }
    }

    &-icon {
        width: 100%;
        height: 100%;
        max-width: 44px;
        max-height: 44px;
        object-fit: contain;
        object-position: center;

        &.d-none {
            display: none;
        }
    }
}
