@use '../utilities/variables';
@use '../utilities/mixins';
.genre {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    border: none;
    box-sizing: border-box;
    cursor: pointer;
    white-space: nowrap;
    background: variables.$button-white;
    min-height: 36px;
    position: relative;
    span {
      position: relative;
      z-index: 1;
    }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 8px;
      background: linear-gradient(275deg, #FFD233 0.95%, #FEEC49 47.84%, #FFD233 98.64%);
      opacity: 0;
      transition: all .3s ease-in-out;
    }
    @media (min-width: 1250px) {
      &:hover {
        &::before {
          opacity: 1;
        }
      }
    }
    &s {
        &-grid {
            display: flex;
            justify-content: space-between;
            gap: 6px;
            ul {
              margin: -10px 0 0 -10px;
              padding: 0;
              list-style: none;
              display: flex;
              flex-wrap: wrap;
              @media (max-width: 767px) {
                width: 700px;
                overflow-x: auto;
              }
              li {
                margin: 10px 0 0 10px;
              }
              .genre-button {
                margin: 0;
                @media (max-width: 767px) {
                  min-height: 32px;
                  padding: 0 12px;
                }
              }
            }
            @media all and (max-width: 767px) {
                display: grid;
                grid-template-rows: repeat(2, max-content);
                grid-auto-flow: column;
                gap: 6px;
                overflow-x: auto;
                overflow-y: hidden;
                white-space: nowrap;
                box-sizing: border-box;
                height: max-content;
                margin: 0 -16px;
                padding: 0 16px 10px;

                &::-webkit-scrollbar {
                  height: 4px;
                  background-color: #e0e0e0;
                  border-radius: 4px;
                }

                &::-webkit-scrollbar-thumb {
                  border-radius: 10px;
                  background: #9d9aa2;
                }

                &::-webkit-scrollbar-track {
                  background-color: #f3f4f6;
                  border-radius: 4px;
                }
            }
        }
    }
}
